let isMouseDown = false;
let currentElement = null;
let mouseDownElement = null;

function handleCursor(x, y, clicked) {
    const element = document.elementFromPoint(x, y);
    if (!element) return;

    // Create a new mouse event object for mousemove
    const mouseMoveEvent = new MouseEvent("mousemove", {
        clientX: x,
        clientY: y,
        view: window,
        bubbles: true,
        cancelable: true,
    });

    // Dispatch the mousemove event
    element.dispatchEvent(mouseMoveEvent);

    // If the current element is different from the previous one
    if (element !== currentElement) {
        if (currentElement) {
            const mouseOutEvent = new MouseEvent("mouseout", {
                clientX: x,
                clientY: y,
                view: window,
                bubbles: true,
                cancelable: true,
                relatedTarget: element,
            });

            // Dispatch the mouseout event on the previous element
            currentElement.dispatchEvent(mouseOutEvent);
        }

        if (element) {
            const mouseOverEvent = new MouseEvent("mouseover", {
                clientX: x,
                clientY: y,
                view: window,
                bubbles: true,
                cancelable: true,
                relatedTarget: currentElement,
            });

            // Dispatch the mouseover event on the new element
            element.dispatchEvent(mouseOverEvent);
        }

        currentElement = element;
    }

    // If clicked is true and the mouse button was not previously down
    if (clicked && !isMouseDown) {
        isMouseDown = true;
        mouseDownElement = element; // Store the element where the mousedown occurred
        const mouseDownEvent = new MouseEvent("mousedown", {
            clientX: x,
            clientY: y,
            view: window,
            bubbles: true,
            cancelable: true,
        });

        // Dispatch the mousedown event
        element.dispatchEvent(mouseDownEvent);
    }

    // If clicked is false and the mouse button was previously down
    if (!clicked && isMouseDown) {
        isMouseDown = false;

        const mouseUpEvent = new MouseEvent("mouseup", {
            clientX: x,
            clientY: y,
            view: window,
            bubbles: true,
            cancelable: true,
        });

        // Dispatch the mouseup event
        element.dispatchEvent(mouseUpEvent);

        // Dispatch the click event only if the mouseup occurred on the same element as the mousedown
        if (element === mouseDownElement) {
            const clickEvent = new MouseEvent("click", {
                clientX: x,
                clientY: y,
                view: window,
                bubbles: true,
                cancelable: true,
            });

            // Dispatch the click event
            element.dispatchEvent(clickEvent);
        }

        mouseDownElement = null; // Reset the mouseDownElement
    }
}

export { handleCursor };
